
.user-account {
    position: fixed;
    z-index: 110;
    top: 0;
    left: 0;
    right: 0;
    background: conic-gradient(at right bottom, rgb(254, 249, 195), rgb(253, 224, 71), rgb(234, 179, 8));
    border: 1px solid black;
    border-top: none;
    height: 600px;
    width: 40vw;
    margin: 117px 0 0 60vw;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.1);
    opacity: 0.95;
}

.close-account {
    text-align: right;
    margin: 0 1rem;
    
}

.close-account span {
    cursor: pointer;
    font-size: 2rem;
    color: red;
    text-decoration: underline;
    margin-right: 1rem;

    &:hover {
        color: darkred;
    }
}

.user {
    text-align: center;
    padding: 2rem;
    font-size: 2.5rem;
    font-weight: 700;
}

.form-div {
    display: flex;
    margin-left: 4rem;
}

.form-div div form {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 700px;
    height: 400px;
}

.form-div div form h3{
    text-align: center;
    padding: 1rem 0;
    font-size: 2rem;
    color: whitesmoke;
}

.form-div div form input {
    width: 90%;
    height: 50px;
    margin-bottom: 20px;
    border: none;
    outline: none;
    font-size: 1.5rem;
    border-radius: 1rem;

    &::placeholder {
        padding-left: 1rem;
    }
}

.form-div div form button {
    width: 200px;
    height: 70px;
    margin: auto;
    border: none;
    border-radius: 2rem;
    background: linear-gradient(to right, rgb(153, 27, 27), rgb(202, 138, 4), rgb(234, 179, 8));
    font-size: 2rem;
    color: whitesmoke;
}

.form-div p {
    text-align: center;
    font-size: 1.5rem;
}

.form-div p span {
    cursor: pointer;
    
    &:hover {
        color: darkred;
    }
}


.hidden {
    display: none;
}

@media only screen and (max-width: 1440px) {
    
    .form-div div form {
        width: 500px;
    }
    .form-div div form button {
        scale: 0.8;
    }
}

@media only screen and (max-width: 1024px) {

    .close-account span {
        font-size: 1.75rem;
    }

    .user p {
        font-size: 2rem;
    }
    
    .form-div div form {
        width: 300px;
    }
    .form-div div form button {
        scale: 0.7;
    }

    .form-div div form p {
        font-size: 1.25rem;
    }
}