
.navbar-div {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background-color: whitesmoke;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
    padding: 2rem;
    z-index: 100;
    justify-content: space-between;
    align-items: center;
}

.navbar-div .logo {
    font-size: 2.5rem;
    text-decoration: none;
    margin-left: 100px;
    cursor: pointer;
}

nav a {
    margin-left: 5rem;
    font-size: 1.75rem;
    color: grey;
    cursor: pointer;

    &:hover {
        color: #4a4a4a;
    }
}

.icons {
    display: flex;
    align-items: center;
    font-size: 2rem;
    font-weight: 700;
    margin-right: 1.5rem;
}

.icons img {
    width: 35px;
    margin-left: 30px;
    cursor: pointer;
}

.icons span {
    margin-left: 5px;
}

@media only screen and (max-width: 1024px) {

    .navbar-div .logo {
        font-size: 1.5rem;
        margin-left: 50px;
    }

    nav a {
        font-size: 1.25rem;
        margin-left: 3rem;
    }

    .icons img {
        width: 30px;
        margin-left: 20px;
    }
}