
.cart-window {
    position: fixed;
    z-index: 110;
    top: 0;
    left: 0;
    right: 0;
    background: conic-gradient(at right bottom, rgb(254, 249, 195), rgb(253, 224, 71), rgb(234, 179, 8));
    border: 1px solid black;
    border-top: none;
    height: 600px;
    width: 40vw;
    margin: 117px 0 0 60vw;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.1);
    opacity: 0.95;
}

.close-cart {
    text-align: right;
    margin: 0 1rem;
    
}

.close-cart span {
    cursor: pointer;
    font-size: 2rem;
    color: red;
    text-decoration: underline;
    margin-right: 1rem;

    &:hover {
        color: darkred;
    }
}

.cart-window h1 {
    text-align: center;
    font-size: 3rem;
    margin: 3rem 0 2rem;
}

.cart-frame {
    display: flex;
    flex-direction: column;
    width: 90%;
    height: 370px;
    background-color: white;
    margin: 0 5%;
    border-radius: 15px;
}

.basket {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: 2rem 1rem;
    height: 71%;
}

.item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-size: 1.5rem;
    background-color: whitesmoke;
}

.item img {
    height: 28px;
    cursor: pointer;
}

.cart-total {
    font-size: 1.5rem;
    font-weight: 700;
    text-align: end;
    margin-right: 1rem;
}

.empty-text {
    font-size: 2.5rem;
    text-align: center;
    margin-top: 8rem;
}

.hidden {
    display: none;
}