
/* style for the fixed background and title */
.background-img {
    width: 100vw;
}

.background-img img {
    margin-top: 100px;
    width: 100vw;
    z-index: -1;
    position: fixed;
}

.background-img h1 {
    position: absolute;
    top: 50%;
    left: 50%;
    text-align: center;
    transform: translate(-50%, -50%);
    z-index: 10;
    font-size: 9rem;
    color: whitesmoke;
}

.background-img p {
    position: absolute;
    top: 75%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    font-size: 3rem;
    color: black;
    border: 1px solid black;
    padding: 0.5rem 1rem;
    background-color: whitesmoke;
}

/* Styling for the about section */
.about-div {
    position: absolute;
    height: 50vh;
    width: 100%;
    top: 110vh;
    background-color: whitesmoke;
    z-index: 11;
    text-align: center;
}

.divider {
    width: 100vw;
    height: 20vh;
    border-radius: 50%;
    background-color: whitesmoke;
    position: absolute;
}

.about-div h1 {
    font-size: 5rem;
    margin: 2rem 0;
}

.about-div p {
    font-size: 2rem;
    width: 70%;
    margin: auto;
}

/* Styling for the menu and pizzas */
.menu-div {
    position: absolute;
    top: 250vh;
    height: 120vh;
    width: 100vw;
    background-color: whitesmoke;
}

.menu-div h1 {
    text-align: center;
    font-size: 5rem;
    margin: 2rem 0;
}

.pizza-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: center;
}

.pizza {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 4rem;
    row-gap: 15px;
    background-color: white;
    width: 550px;
    margin-left: 50px;
    border-radius: 2rem;
    z-index: 10;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);

}

.pizza h3 {
    font-size: 4rem;
}

.pizza p {
    font-size: 2rem;
}

.pizza img {
    width: 400px;
}

.pizza button {
    width: 250px;
    height: 80px;
    font-size: 2rem;
    border-radius: 2rem;
    background-color: greenyellow;
    margin-bottom: 20px;
    cursor: pointer;

    &:hover  {
        background-color: limegreen;
    }
}

/* Styling for the order section */
.order-div {
    position: absolute;
    top: 460vh;
    width: 100vw;
    height: 100vh;
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    z-index: 10;
}

.order-div h1 {
    text-align: center;
    font-size: 5rem;
    margin: 4rem 0;
}

.order-form-div {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 0 10%;
    height: 65%;
    background-color: white;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
}

.order-cart {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 50px;
    width: 96%;
    height: 100px;
    margin: 30px 2%;
    background-color: whitesmoke;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
}

.order-cart p {
    font-size: 2rem;
}

.order-form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    width: 90%;
    margin: 1rem 5%;
}

.form-group {
    display: flex;
    flex-direction: column;
}

.form-group label {
    margin-bottom: 8px;
    font-size: 1.5rem;
}

.form-group input, select {
    padding: 8px;
    font-size: 1rem;
    border: none;
    border-bottom: 3px solid lightgrey;
    outline: none;
    background-color: whitesmoke;
    border-radius: 5px;
}

.form-group input {
    height: 50px;
    font-size: 1.75rem;
}

.form-group select {
    height: 67px;
    font-size: 1.75rem;
}

.order-form-div button {
    width: 90%;
    margin: 2rem 5%;
    height: 80px;
    font-size: 2.5rem;
    border-radius: 1rem;
    border: none;
    background-color: lightgreen;

    &:hover {
        background-color: limegreen;
    }
}

/* Styling for the faq section */
.faq-div {
    position: absolute;
    top: 600vh;
    width: 100vw;
    height: 20vh;
    background-color: whitesmoke;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.faq-group {
    margin: 0 15%;
    display: flex;
    gap: 2rem;
    font-size: 1.75rem;
    justify-content: center;
    align-items: center;
}

.faq-group a {
    color: darkgrey;
    cursor: pointer;
    width: 100%;
    &:hover {
        color: grey;
    }
}

.faq-group img {
    width: 40px;
    cursor: pointer;
}


/* Positioning the dividers */
.one {
    top: 100vh;
}

.two {
    top: 150vh;
}
.three {
    top: 240vh;
}
.four {
    top: 360vh;
}
.five {
    top: 450vh;
}
.six {
    top: 550vh;
}
.seven {
    top: 590vh;
}


@media only screen and (max-width: 1440px) {
    .background-img h1{
        font-size: 6rem;
    }

    .background-img p {
        top: 62%;
        font-size: 2rem;
    }

    .about-div {
        h1 {
            font-size: 3.4rem;
        }
        p {
            font-size: 1.4rem;
        }
    }

    .menu-div {
        h1 {
            font-size: 3.4rem;
        }
        .pizza-grid {
            grid-template-columns: repeat(2, 1fr);
            gap: 1rem;
            margin: 0;
            align-items: center;
        }
        .pizza {
            display: flex;
            width: 400px;
            margin: 0 auto;
            row-gap: 10px;

            h3 {
                font-size: 3rem;
            }
            p{
                font-size: 1.75rem;
            }
            img {
                width: 300px;
            }
            button {
                scale: 0.7;
            }
        }
    }

    .faq-group a {
        font-size: 1.25rem;
    }
}

@media only screen and (max-width: 1024px) {
    .background-img h1{
        font-size: 4.5rem;
    }

    .background-img p {
        top: 60%;
        font-size: 1.25rem;
    }

    .background-img img {
        width: 140%;
    }
}