.orders-window {
    position: fixed;
    z-index: 110;
    top: 0;
    left: 0;
    right: 0;
    background: conic-gradient(at right bottom, rgb(254, 249, 195), rgb(253, 224, 71), rgb(234, 179, 8));
    border: 1px solid black;
    border-top: none;
    height: 600px;
    width: 40vw;
    margin: 117px 0 0 60vw;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.1);
    opacity: 0.95;
    overflow-y: scroll;
}

.close-orders {
    text-align: right;
    margin: 0 1rem;
    
}

.close-orders span {
    cursor: pointer;
    font-size: 2rem;
    color: red;
    text-decoration: underline;
    margin-right: 1rem;

    &:hover {
        color: darkred;
    }
}

.orders-window h1 {
    text-align: center;
    font-size: 3rem;
    margin: 2rem 0;
}

.orders-view {
    width: 90%;
    height: 400px;
    background-color: white;
    margin: 0 5%;
    display: flex;
    flex-direction: column;
    border-radius: 15px;
    overflow-y: scroll;
}

.order {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: whitesmoke;
    margin: 1rem 0;
    padding: 0.5rem 2rem;
    min-height: 90px;
}

.order h3 {
    font-size: 1.5rem;
}

.order p {
    font-size: 1.25rem;
}

.orders-empty {
    text-align: center;
    margin: 7rem 0;
    font-size: 3rem;
}

.hidden {
    display: none;
}